import { reactive } from "vue";
import { storeToRefs } from "pinia";
import { ReactiveVariable } from "vue/macros";
import {
  FormFieldProps,
  FormFieldSelectProps,
  LacSelectOption,
} from "@/views/components/form/types";
import { useFormValidate } from "@/use/useFormValidate";
import { requiredRules } from "@/kernel/validation/vuelidate/requiredRules";
import { phoneRules } from "@/kernel/validation/vuelidate/phoneRules";
import { emailRules } from "@/kernel/validation/vuelidate/emailRules";
import { maxLengthRules } from "@/kernel/validation/vuelidate/maxLengthRules";
import { useRouter } from "vue-router";
import { AgentEntity } from "@/kernel/entities/Agent";
import { PhoneForm } from "@/kernel/entities/types";
import { useStoreAgent } from "@/store/agent";
import { useStore } from "@/store";

export const useAgentForm = (employeeRoles: string[]) => {
  const router = useRouter();
  const storeAgent = useStoreAgent();
  const { setFieldRef, validateForm, resetValidations } = useFormValidate();
  const { dictionary } = storeToRefs(useStore());

  const selectOptions = employeeRoles.map((el) => ({ value: el, text: el }));

  const employeeRole: ReactiveVariable<FormFieldSelectProps> = reactive({
    value: null,
    id: "role",
    label: "Должность",
    items: selectOptions,
    rules: requiredRules(),
  });

  const agentPhone: ReactiveVariable<PhoneForm[]> = reactive([]);

  const fields: ReactiveVariable<Record<string, FormFieldProps>> = reactive({
    name: {
      value: "",
      id: "name",
      label: "ФИО",
      rules: requiredRules(),
    },
    phone: {
      value: "",
      id: "phone",
      label: "Телефон",
      rules: phoneRules(),
    },
    dob: {
      value: "",
      id: "dob",
      label: "Дата рождения",
    },
    email: {
      value: "",
      id: "email",
      label: "E-mail",
      rules: emailRules(),
    },
    comment: {
      value: "",
      id: "comment",
      rules: maxLengthRules(),
    },
  });

  function prepareData(): any {
    const res: Record<string, any> = {};

    Object.entries(fields).forEach(([key, value]) => {
      if (value.value) {
        res[key] = value.value;
      }
    });

    res.phone = [];
    for (const phone of agentPhone) {
      if (phone.phone && phone.type.value) {
        res.phone.push(phone);
      }
    }

    res.role = employeeRole.value
      ? (employeeRole.value as LacSelectOption).value
      : "";

    return res as any;
  }

  function removeAgentPhone(index: number) {
    if (agentPhone.length > 1) {
      agentPhone.splice(index, 1);
    }
  }

  function addAgentPhone(phone?: PhoneForm[]) {
    if (phone) {
      for (const p of phone) {
        agentPhone.push({
          phone: p.phone,
          type: p.type,
          extra: p.extra,
        });
      }
    } else {
      agentPhone.push({
        phone: "",
        type: { value: "work", text: dictionary.value.phone.type["work"] },
        extra: "",
      });
    }
  }

  function clear() {
    Object.values(fields).forEach((el) => (el.value = ""));
    employeeRole.value = null;
    resetValidations();
  }

  function successCallback(variant: string) {
    router.back();
  }

  function fillFromSelectedAgent(agent: AgentEntity) {
    employeeRole.value =
      selectOptions.find((el) => el.value === agent.role) || null;

    addAgentPhone(agent.phone);

    fields.name.value = agent.name;
    fields.dob.value = agent.dob || "";
    fields.email.value = agent.email || "";
    fields.comment.value = agent.comment || "";
  }

  async function addAgent(payload: any) {
    await storeAgent.add(payload);
  }

  async function editAgent(payload: any) {
    await storeAgent.edit(payload);
  }

  async function getAgent(id: number) {
    return await storeAgent.get(id);
  }

  return {
    employeeRole,
    agentPhone,
    fields,
    successCallback,
    prepareData,
    setFieldRef,
    validateForm,
    fillFromSelectedAgent,
    addAgent,
    editAgent,
    getAgent,
    addAgentPhone,
    removeAgentPhone,
  };
};
