import { getHttp } from "@/kernel/api/http";
import { ApiResponseType, ApiRequestListOptionsType } from "@/kernel/api/types";
import { NotRequestException } from "@/kernel/lib/exceptions/not-request.exception";
import { RoleAddData } from "@/kernel/api/dto/AgentRole";

export const getAgentRoleDelete = async (
  role: string
): Promise<ApiResponseType> => {
  const res = await getHttp().get<ApiResponseType | false>(
    "/agent/role/delete",
    {
      searchParams: {
        role,
      },
    }
  );

  if (!res) {
    throw new NotRequestException(getAgentRoleDelete.name);
  }

  return res;
};

export const getAgentRoleList = async (
  params: ApiRequestListOptionsType = {}
): Promise<ApiResponseType<string[]>> => {
  const res = await getHttp().get<ApiResponseType<string[]> | false>(
    "/agent/role/list",
    { searchParams: { ...params, size: 30 } }
  );

  if (!res) {
    throw new NotRequestException(getAgentRoleList.name);
  }

  return res;
};

export const postAgentRoleAdd = async (
  payload: RoleAddData
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>(
    "/agent/role/add",
    payload
  );

  if (!res) {
    throw new NotRequestException(postAgentRoleAdd.name);
  }

  return res;
};

export const postAgentRoleEdit = async (
  oldRole: string,
  newRole: string
): Promise<ApiResponseType> => {
  const res = await getHttp().post<ApiResponseType | false>(
    "/agent/role/edit",
    {
      old_role: oldRole,
      new_role: newRole,
    }
  );

  if (!res) {
    throw new NotRequestException(postAgentRoleEdit.name);
  }

  return res;
};
