import {
  helpers,
  maxLength,
  minLength,
  numeric,
  required,
} from "@vuelidate/validators";

const clear = (value: string): string => value.replace(/\D/g, "");

export const phoneRules = () => ({
  required: helpers.withMessage("Это поле обязательно", required),
  minLength: helpers.withMessage(
    "Длина телефона должна быть не менее 10 символов",
    (value: string) => clear(value).length >= 10
  ),
  maxLength: helpers.withMessage(
    "Длина телефона должна быть не более 12 символов",
    (value: string) => clear(value).length <= 12
  ),
});
